$block-size: 300px;
$colors:
  "1" rgb(255, 0, 0),
  "2" rgb(204, 0, 0),
  "3" rgb(153, 0, 0),
  "4" rgb(102, 0, 0),
  "5" rgb(51, 0, 0),
  "6" rgb(0, 0, 0);

.container {
  display: grid;
  grid-template-columns: repeat(1, minmax($block-size, 1fr));

  @media (min-width: 600px) {
    grid-template-columns: repeat(2, minmax($block-size, 1fr));
  }

  @media (min-width: 900px) {
    grid-template-columns: repeat(3, minmax($block-size, 1fr));
  }

}
    
.block {
  height: $block-size;
  line-height: $block-size;
  color: white;
  text-align: center;

  @each $num, $color in $colors {
    &:nth-child(#{$num}) {
      background-color: $color;
    }
  }
}
